import preventDbClick from "./index.vue";

// 
const myPlugin = {
  // eslint-disable-next-line
  version:__VERSION__,
  // install
  // Vue
  // eslint-disable-next-line
  install(Vue, options) {
    // vue'gantt'
    Vue.component("v-prevent-dbclick", preventDbClick);
  }
};
// 
if (typeof window !== "undefined" && window.Vue) {
  window.Vue.use(myPlugin);
}

// main.jsVue.use()
export default myPlugin;
